body {
  color:rgb(36, 35, 35);
  text-align: center;
}

.notFoundPage {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 2em;
  padding: 20px;
  margin: 20px;
}

.notFoundPage h1, h2, h3 {
  margin: 0 auto;
}

#code {
  font-size: 3em;
  color: rgb(233, 75, 75);
  border: 6px solid rgb(233, 75, 75);
  padding: 10px;
}

.ghost {
  height: 200px;
  width: 200px;
  margin: 0 auto;
}



@media all and (max-width: 600px) {

  .notFoundPage {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    font-size: 1em;
  }
  
  .notFoundPage h1, .notFoundPage h2, .notFoundPage h3 {
    margin-top: 20px;
  }
  
  #code {
    font-size: 2em;
    padding: 5px;
  }

}


